"use client";

import GoogleSignIn from "@/components/GoogleSignIn";
import PhoneSignIn from "@/components/PhoneSignIn";
import { useState } from "react";

export default function LoginPage() {
  const [authMethod, setAuthMethod] = useState<'google' | 'phone'>('google');

  return (
    <div className="flex flex-col items-center justify-center min-h-screen p-6">
      <div className="w-full max-w-md p-8 space-y-8 bg-white rounded-lg shadow-md">
        <h1 className="text-2xl font-bold text-center">Sign in to your account</h1>
        
        <div className="flex justify-center space-x-4 mt-4">
          <button 
            onClick={() => setAuthMethod('google')}
            className={`px-4 py-2 rounded-lg ${authMethod === 'google' ? 'bg-blue-600 text-white' : 'bg-gray-200 text-gray-800'}`}
          >
            Google
          </button>
          <button 
            onClick={() => setAuthMethod('phone')}
            className={`px-4 py-2 rounded-lg ${authMethod === 'phone' ? 'bg-blue-600 text-white' : 'bg-gray-200 text-gray-800'}`}
          >
            Phone Number
          </button>
        </div>
        
        {authMethod === 'google' ? (
          <>
            <p className="text-center text-gray-600 mt-4">
              Use your Google account to sign in
            </p>
            <div className="flex justify-center mt-6">
              <GoogleSignIn />
            </div>
          </>
        ) : (
          <>
            <p className="text-center text-gray-600 mt-4">
              Use your phone number to sign in
            </p>
            <div className="flex justify-center mt-6">
              <PhoneSignIn />
            </div>
          </>
        )}
      </div>
    </div>
  );
}